.monaco-menu .monaco-action-bar.vertical {
  margin-left: 0;
  overflow: visible;
}

.monaco-menu .monaco-action-bar.vertical .actions-container {
  display: block;
}

.monaco-menu .monaco-action-bar.vertical .action-item {
  padding: 0;
  transform: none;
  display: flex;
}

.monaco-menu .monaco-action-bar.vertical .action-item.active {
  transform: none;
}

.monaco-menu .monaco-action-bar.vertical .action-menu-item {
  flex: 1 1 auto;
  display: flex;
  height: 2em;
  align-items: center;
  position: relative;
}

.monaco-menu .monaco-action-bar.vertical .action-label {
  flex: 1 1 auto;
  text-decoration: none;
  padding: 0 1em;
  background: none;
  font-size: 12px;
  line-height: 1;
}

.monaco-menu .monaco-action-bar.vertical .keybinding,
.monaco-menu .monaco-action-bar.vertical .submenu-indicator {
  display: inline-block;
  flex: 2 1 auto;
  padding: 0 1em;
  text-align: right;
  font-size: 12px;
  line-height: 1;
}

.monaco-menu .monaco-action-bar.vertical .submenu-indicator {
  height: 100%;
  mask: url("data:image/svg+xml;base64,") no-repeat 90% 50%/13px 13px;
  -webkit-mask: url("data:image/svg+xml;base64,") no-repeat 90% 50%/13px 13px;
}

.monaco-menu .monaco-action-bar.vertical .action-item.disabled .keybinding,
.monaco-menu .monaco-action-bar.vertical .action-item.disabled .submenu-indicator {
  opacity: 0.4;
}

.monaco-menu .monaco-action-bar.vertical .action-label:not(.separator) {
  display: inline-block;
  box-sizing: border-box;
  margin: 0;
}

.monaco-menu .monaco-action-bar.vertical .action-item {
  position: static;
  overflow: visible;
}

.monaco-menu .monaco-action-bar.vertical .action-item .monaco-submenu {
  position: absolute;
}

.monaco-menu .monaco-action-bar.vertical .action-label.separator {
  padding: 0.5em 0 0 0;
  margin-bottom: 0.5em;
  width: 100%;
}

.monaco-menu .monaco-action-bar.vertical .action-label.separator.text {
  padding: 0.7em 1em 0.1em 1em;
  font-weight: bold;
  opacity: 1;
}

.monaco-menu .monaco-action-bar.vertical .action-label:hover {
  color: inherit;
}

.monaco-menu .monaco-action-bar.vertical .menu-item-check {
  position: absolute;
  visibility: hidden;
  mask: url("data:image/svg+xml;base64,") no-repeat 50% 56%/15px 15px;
  -webkit-mask: url("data:image/svg+xml;base64,") no-repeat 50% 56%/15px 15px;
  width: 1em;
  height: 100%;
}

.monaco-menu .monaco-action-bar.vertical .action-menu-item.checked .menu-item-check {
  visibility: visible;
}

/* Context Menu */
.context-view.monaco-menu-container {
  outline: 0;
  border: none;
  animation: fadeIn 0.083s linear;
}

.context-view.monaco-menu-container :focus,
.context-view.monaco-menu-container .monaco-action-bar.vertical:focus,
.context-view.monaco-menu-container .monaco-action-bar.vertical :focus {
  outline: 0;
}

.monaco-menu .monaco-action-bar.vertical .action-item {
  border: thin solid transparent;
  /* prevents jumping behaviour on hover or focus */
}

/* High Contrast Theming */
.hc-black .context-view.monaco-menu-container {
  box-shadow: none;
}

.hc-black .monaco-menu .monaco-action-bar.vertical .action-item.focused {
  background: none;
}

/* Menubar styles */
.menubar {
  display: flex;
  flex-shrink: 1;
  box-sizing: border-box;
  height: 30px;
  overflow: hidden;
  flex-wrap: wrap;
}

.fullscreen .menubar:not(.compact) {
  margin: 0px;
  padding: 0px 5px;
}

.menubar > .menubar-menu-button {
  align-items: center;
  box-sizing: border-box;
  padding: 0px 8px;
  cursor: default;
  -webkit-app-region: no-drag;
  zoom: 1;
  white-space: nowrap;
  outline: 0;
}

.menubar.compact {
  flex-shrink: 0;
}

.menubar.compact > .menubar-menu-button {
  width: 100%;
  height: 100%;
  padding: 0px;
}

.menubar .menubar-menu-items-holder {
  position: absolute;
  left: 0px;
  opacity: 1;
  z-index: 2000;
}

.menubar .menubar-menu-items-holder.monaco-menu-container {
  outline: 0;
  border: none;
}

.menubar .menubar-menu-items-holder.monaco-menu-container :focus {
  outline: 0;
}

.menubar .toolbar-toggle-more {
  background-position: center;
  background-repeat: no-repeat;
  background-size: 14px;
  width: 20px;
  height: 100%;
}

.menubar.compact .toolbar-toggle-more {
  position: absolute;
  left: 0px;
  top: 0px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 16px;
  cursor: pointer;
  width: 100%;
}

.menubar .toolbar-toggle-more {
  display: inline-block;
  padding: 0;
  mask: url("data:image/svg+xml;base64,") no-repeat 50% 55%/14px 14px;
  -webkit-mask: url("data:image/svg+xml;base64,") no-repeat 50% 55%/14px 14px;
}

