.monaco-editor .zone-widget {
  position: absolute;
  z-index: 10;
}

.monaco-editor .zone-widget .zone-widget-container {
  border-top-style: solid;
  border-bottom-style: solid;
  border-top-width: 0;
  border-bottom-width: 0;
  position: relative;
}