.monaco-editor .peekview-widget .head {
  box-sizing: border-box;
  display: flex;
}

.monaco-editor .peekview-widget .head .peekview-title {
  display: flex;
  align-items: center;
  font-size: 13px;
  margin-left: 20px;
  cursor: pointer;
}

.monaco-editor .peekview-widget .head .peekview-title .dirname:not(:empty) {
  font-size: 0.9em;
  margin-left: 0.5em;
}

.monaco-editor .peekview-widget .head .peekview-title .meta {
  white-space: nowrap;
}

.monaco-editor .peekview-widget .head .peekview-title .meta:not(:empty)::before {
  content: "-";
  padding: 0 0.3em;
}

.monaco-editor .peekview-widget .head .peekview-actions {
  flex: 1;
  text-align: right;
  padding-right: 2px;
}

.monaco-editor .peekview-widget .head .peekview-actions > .monaco-action-bar {
  display: inline-block;
}

.monaco-editor .peekview-widget .head .peekview-actions > .monaco-action-bar,
.monaco-editor .peekview-widget .head .peekview-actions > .monaco-action-bar > .actions-container {
  height: 100%;
}

.monaco-editor .peekview-widget .head .peekview-actions > .monaco-action-bar .action-item {
  margin-left: 4px;
}

.monaco-editor .peekview-widget .head .peekview-actions > .monaco-action-bar .action-label {
  width: 16px;
  height: 100%;
  margin: 0;
  line-height: inherit;
  background-repeat: no-repeat;
  background-position: center center;
}

.monaco-editor .peekview-widget .head .peekview-actions > .monaco-action-bar .action-label.codicon {
  margin: 0;
}

.monaco-editor .peekview-widget > .body {
  border-top: 1px solid;
  position: relative;
}

.monaco-editor .peekview-widget .head .peekview-title .codicon {
  margin-right: 4px;
}