.monaco-custom-checkbox {
  margin-left: 2px;
  float: left;
  cursor: pointer;
  overflow: hidden;
  opacity: 0.7;
  width: 20px;
  height: 20px;
  border: 1px solid transparent;
  padding: 1px;
  box-sizing: border-box;
  user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.monaco-custom-checkbox:hover,
.monaco-custom-checkbox.checked {
  opacity: 1;
}

.hc-black .monaco-custom-checkbox {
  background: none;
}

.hc-black .monaco-custom-checkbox:hover {
  background: none;
}

.monaco-custom-checkbox.monaco-simple-checkbox {
  height: 18px;
  width: 18px;
  border: 1px solid transparent;
  border-radius: 3px;
  margin-right: 9px;
  margin-left: 0px;
  padding: 0px;
  opacity: 1;
  background-size: 16px !important;
}

.monaco-custom-checkbox.monaco-simple-checkbox.checked {
  background: url("data:image/svg+xml;base64,") center center no-repeat;
}

.monaco-custom-checkbox.monaco-simple-checkbox.checked {
  background: url("data:image/svg+xml;base64,") center center no-repeat;
}