.cf-resource-card--contents {
  flex-direction: column;
  flex: 1 0 0;
}

.cf-resource-card--row {
  margin-bottom: 3px;
}

.cf-resource-card--toggle {
  padding-right: 16px;
}

.cf-resource-card--meta {
  display: flex;
  align-items: center;
  font-size: 11px;
  font-weight: 600;
  color: #999dab;
  padding-bottom: 2px;
}

.cf-resource-card--meta-item {
  transition: border-color 0.25s ease, color 0.25s ease;
  border-right: 2px solid #383846;
  padding-right: 12px;
  margin-right: 12px;
}

.cf-resource-card--meta-item:last-child {
  border-right: 0;
  padding-right: 0;
  margin-right: 0;
}

.cf-resource-card:hover .cf-resource-card--meta-item {
  color: #d4d7dd;
  border-color: #545667;
}

.cf-resource-card--context-menu {
  opacity: 0;
  transition: opacity 0.25s ease;
  position: absolute;
  top: 8px;
  right: 8px;
}

.cf-resource-card:hover .cf-resource-card--context-menu {
  opacity: 1;
}

.cf-resource-card.cf-resource-card__disabled {
  background-color: rgba(41, 41, 51, 0.5);
  color: #757888;
  font-style: italic;
}

.cf-resource-card.cf-resource-card__disabled .cf-resource-card--meta {
  color: #757888;
}

.cf-resource-card.cf-resource-card__disabled:hover .cf-resource-card--meta {
  color: #a4a8b6;
}

.panel .cf-resource-card, .tabs .cf-resource-card {
  background-color: #31313d;
}

.panel .cf-resource-card:hover, .tabs .cf-resource-card:hover {
  background-color: #383846;
}

.panel .cf-resource-card.cf-resource-card__disabled, .tabs .cf-resource-card.cf-resource-card__disabled {
  background-color: rgba(49, 49, 61, 0.5);
}

.cf-right-click-portal {
  z-index: 9995;
  position: fixed;
  top: 0;
  left: 0;
}

.cf-right-click {
  position: fixed;
  z-index: 9999;
  background-color: #202028;
  border-radius: 4px;
  border-style: solid;
  border-width: 2px;
}

.cf-right-click--menu {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  list-style: none;
  margin: 0;
  padding: 0;
}

.cf-right-click--menu-item {
  padding: 6px 8px;
  font-size: 1em;
  line-height: 1em;
  color: #999dab;
  white-space: nowrap;
  font-weight: 600;
  user-select: none;
  margin: 0;
  transition: color 0.25s ease, background-color 0.25s ease;
}

.cf-right-click--menu-item:first-child {
  border-radius: 3px 3px 0 0;
}

.cf-right-click--menu-item:last-child {
  border-radius: 0 0 3px 3px;
}

.cf-right-click--menu-item:hover {
  cursor: pointer;
  color: #fff;
  background-color: #383846;
}

.cf-right-click--menu-item.cf-right-click--menu-item__disabled, .cf-right-click--menu-item.cf-right-click--menu-item__disabled:hover {
  cursor: default;
  font-style: italic;
  color: #676978;
  background-color: transparent;
}

.cf-right-click--divider {
  height: 2px;
  width: 100%;
  background-color: #383846;
  margin: 2px 0;
}

.cf-right-click__default {
  border-color: #999dab;
  box-shadow: 0 0 5px 0 #676978;
}

.cf-right-click__primary {
  border-color: #22adf6;
  box-shadow: 0 0 5px 0 #4591ed;
}

.cf-right-click__secondary {
  border-color: #9394ff;
  box-shadow: 0 0 5px 0 #7a65f2;
}

.cf-right-click__success {
  border-color: #7ce490;
  box-shadow: 0 0 5px 0 #4ed8a0;
}

.cf-right-click__warning {
  border-color: #ffd255;
  box-shadow: 0 0 5px 0 #f48d38;
}

.cf-right-click__danger {
  border-color: #ff8564;
  box-shadow: 0 0 5px 0 #dc4e58;
}

.cf-status-indicator {
  top: 0;
  right: 0;
  position: absolute;
  z-index: 3;
  pointer-events: none;
}

.cf-status-indicator--child {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
}

.cf-status-indicator--shadow {
  position: absolute;
  top: 2px;
  right: 2px;
  bottom: 2px;
  border-radius: 0 3px 3px 0;
  background: rgba(32, 32, 40, 0);
  background: -moz-linear-gradient(left, rgba(32, 32, 40, 0) 0%, #202028 100%);
  background: -webkit-linear-gradient(left, rgba(32, 32, 40, 0) 0%, #202028 100%);
  background: linear-gradient(to right, rgba(32, 32, 40, 0) 0%, #202028 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="$startColor", endColorstr="$endColor",GradientType=1 );
  transition: opacity 0.25s ease;
  border-right-color: #202028;
  border-right-style: solid;
  opacity: 0;
}

.cf-status-indicator__valid .cf-status-indicator--shadow, .cf-status-indicator__error .cf-status-indicator--shadow, .cf-status-indicator__loading .cf-status-indicator--shadow {
  opacity: 1;
}

@keyframes StatusSpinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.cf-status-indicator--spinner {
  border-style: solid;
  border-radius: 50%;
  animation-duration: 0.85s;
  animation-name: StatusSpinner;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  border: 2px solid #383846;
  border-top-color: #22adf6;
  width: 1.1em;
  height: 1.1em;
}

.cf-status-indicator__xs {
  width: 22px;
  height: 22px;
  font-size: 11px;
}

.cf-status-indicator__xs .cf-status-indicator--shadow {
  width: 33.2px;
  border-right-width: 13.2px;
}

.cf-status-indicator__sm {
  width: 30px;
  height: 30px;
  font-size: 12px;
}

.cf-status-indicator__sm .cf-status-indicator--shadow {
  width: 46px;
  border-right-width: 18px;
}

.cf-status-indicator__md {
  width: 38px;
  height: 38px;
  font-size: 13px;
}

.cf-status-indicator__md .cf-status-indicator--shadow {
  width: 58.8px;
  border-right-width: 22.8px;
}

.cf-status-indicator__lg {
  width: 46px;
  height: 46px;
  font-size: 17px;
}

.cf-status-indicator__lg .cf-status-indicator--shadow {
  width: 71.6px;
  border-right-width: 27.6px;
}

.cf-status-indicator__valid {
  color: #4ed8a0;
}

.cf-status-indicator__error {
  color: #ff8564;
}