.monaco-editor .iPadShowKeyboard {
  width: 58px;
  min-width: 0;
  height: 36px;
  min-height: 0;
  margin: 0;
  padding: 0;
  position: absolute;
  resize: none;
  overflow: hidden;
  background: url("data:image/svg+xml;base64,") center center no-repeat;
  border: 4px solid #F6F6F6;
  border-radius: 4px;
}

.monaco-editor.vs-dark .iPadShowKeyboard {
  background: url("data:image/svg+xml;base64,") center center no-repeat;
  border: 4px solid #252526;
}