.monaco-editor .glyph-margin {
  position: absolute;
  top: 0;
}

/*
	Keeping name short for faster parsing.
	cgmr = core glyph margin rendering (div)
*/
.monaco-editor .margin-view-overlays .cgmr {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
}