.monaco-editor .margin-view-overlays .line-numbers {
  position: absolute;
  text-align: right;
  display: inline-block;
  vertical-align: middle;
  box-sizing: border-box;
  cursor: default;
  height: 100%;
}

.monaco-editor .relative-current-line-number {
  text-align: left;
  display: inline-block;
  width: 100%;
}

.monaco-editor .margin-view-overlays .line-numbers {
  cursor: -webkit-image-set(url("data:image/svg+xml;base64,") 1x, url("data:image/svg+xml;base64,") 2x) 30 0, default;
}

.monaco-editor.mac .margin-view-overlays .line-numbers {
  cursor: -webkit-image-set(url("data:image/svg+xml;base64,") 1x, url("data:image/svg+xml;base64,") 2x) 24 3, default;
}

.monaco-editor .margin-view-overlays .line-numbers.lh-odd {
  margin-top: 1px;
}